<template>
  <v-card flat tile width="100%" class="white--text text-center">
    <v-img
      class="white--text align-center pt-12"
      height="300px"
      :src="mainImage"
      v-if="mainImage"
    >
      <v-card-text class="text-center">
        <div class="display-1 white--text text-center pa-3">{{ title }}</div>
        <div class="text-center justify-center pt-5" v-if="tabs">
          <v-tabs
            center-active
            fixed-tabs
            dark
            background-color="transparent"
            show-arrows
          >
            <v-tab
              v-for="(tab, index) in tabs"
              :key="index"
              class="title"
              @click="changeTab(tab.id)"
              >{{ tab.title }}</v-tab
            >
          </v-tabs>
          <!-- <div v-for="(tab, index) in tabs" :key="index">
            <v-btn
              class="white--text pa-2 ma-2 subtitle-2"
              :class="{ success: tab.active }"
              outlined
              @click="changeTab(tab.id)"
            >
              {{ tab.title }} 타입
            </v-btn>
          </div> -->
        </div>
      </v-card-text>
    </v-img>
  </v-card>
</template>

<script>
export default {
  props: {
    title: String,
    mainImage: String,
    tabIcon: String,
    tabs: Array
  },
  methods: {
    changeTab(tabId) {
      this.$emit("changeTab", tabId);
    }
  }
};
</script>



