<template>
  <v-navigation-drawer
    v-model="drawerLocal"
    app
    disable-resize-watcher
    temporary
    :width="$vuetify.breakpoint.smAndDown ? '80%' : 400"
    color="white text--darken-5"
  >
    <v-img
      src="http://yaglobal.cafe24.com/boramea/images/nav_top_logo.jpg"
      dark
    >
      <v-row class="fill-height">
        <div></div>
        <v-spacer />
        <v-card-title class="white--text pr-3">
          <div class="display-1 pr-3"></div>
        </v-card-title>
      </v-row>
    </v-img>
    <v-list>
      <v-list-item>
        <v-list-item-action>
          <v-icon>mdi-cube</v-icon>
        </v-list-item-action>
        <v-list-item-content>
          <v-list-item-title class=" title"
            >보라매&nbsp;&nbsp;센트럴바움</v-list-item-title
          >
        </v-list-item-content>
        <v-list-item-icon @click.stop="$emit('changeDrawer', false)"
          ><v-icon>mdi-close</v-icon></v-list-item-icon
        >
      </v-list-item>
      <v-list-group
        v-for="menu in menus"
        :key="menu.title"
        v-model="menu.active"
        :prepend-icon="menu.icon"
        no-action
      >
        <template v-slot:activator>
          <v-list-item-content>
            <v-list-item-title v-text="menu.title"></v-list-item-title>
          </v-list-item-content>
        </template>
        <v-list-item
          v-for="child in menu.submenus"
          :key="child.title"
          :to="child.path"
        >
          <v-list-item-action v-if="child.icon">
            <v-icon>{{ child.icon }}</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title v-text="child.title"> </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list-group>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
export default {
  props: {
    menus: Array,
    drawer: Boolean
  },
  model: {
    prop: "drawer",
    event: "changeDrawer"
  },
  computed: {
    drawerLocal: {
      get: function() {
        return this.drawer;
      },
      set: function(value) {
        this.$emit("changeDrawer", value);
      }
    }
  }
};
</script>



